import crypto from "crypto";

class security {
  constructor() {
    this.hashType = "sha256";
    this.saltCharSet = [32, 126];
    this.saltLength = 8;
    this.pepperCharSet = [97, 122];
  }

  setHashType( hashType ) {
    this.hashType = hashType;
  }

  setSaltCharSet( saltCharSet ) {
    const a = saltCharSet.split( /\s/ );
    this.saltCharSet = [ parseInt( a[0] ) || 32, parseInt( a[1] ) || 126 ];
  }

  setSaltLength( saltLength ) {
    this.saltLength = parseInt( saltLength ) || 8;
  }

  setPepperCharSet( pepperCharSet ) {
    const a = pepperCharSet.split( /\s/ );
    this.pepperCharSet = [ parseInt( a[0] ) || 97, parseInt( a[1] ) || 122 ];
  }

  generateSalt() {
    const sMin = Math.ceil( this.saltCharSet[0] );
    const sMax = Math.floor( this.saltCharSet[1] );
    const seed = ( new Date() ).getUTCMilliseconds();

    let salt = [];
    for( let i = 0; i < this.saltLength; ++i ) {
      const sel = Math.floor( (( Math.floor( Math.random() * (sMax - sMin + 1) + sMin ) + seed ) % (sMax - sMin + 1) ) + sMin );
      salt.push( sel );
    }

    return( String.fromCharCode( ...salt ) );
  }

  generatePepper() {
    const pMin = Math.ceil( this.pepperCharSet[0] );
    const pMax = Math.floor( this.pepperCharSet[1] );
    const pepper = Math.floor( Math.random() * (pMax - pMin + 1) + pMin );
    return( String.fromCharCode( pepper ) );
  }

  generateDigest( input, salt, pepper ) {
    const hash = crypto.createHash( this.hashType );

    if( !!salt && !!pepper ) {
      hash.update( salt + input + pepper );
    }

    else if( !!salt && !pepper ) {
      hash.update( salt + input );
    }

    else {
      hash.update( input );
    }

    // FYI calling digest() stops the hash from being able to update
    const digest = hash.digest( "hex" );
    return( digest );
  }

  compareDigest( digest_DB, password, salt_DB ) {

    // Uncompress charset
    let pepperArray = [];
    for( let i = this.pepperCharSet[0]; i < this.pepperCharSet[1]; ++i ) {
      pepperArray.push( i );
    }

    let digestMatch = false;
    for( let i = 0; i < Math.abs( this.pepperCharSet[0] - this.pepperCharSet[1] ); ++i ) {

      const sel = Math.floor( Math.random() * (pepperArray.length) );
      let pepper = String.fromCharCode( pepperArray[sel] );

      let digest = this.generateDigest( password, salt_DB, pepper );
      console.debug( digest, pepper );

      // -- In reality, an API will also have its' own scheme
      //    ==> nextDigest = generateDigestAPI( prevDigest, nextsalt, peppersAPI ) ...

      pepperArray.splice( sel, 1 );

      if( digest === digest_DB ) {
        digestMatch = true;
        console.info( `Match after ${i} peppers` );
        break;
      }

    }

    console.info( "Digest comparison result:", digestMatch, { peppersLeft: String.fromCharCode( ...pepperArray ) } );
    return( digestMatch );

  }
  
};

export default security;
