import React from "react";
import security from "./security.js";

class LoginPage extends React.Component {
  constructor( props ) {
    super( props );
    this.handleChange = this.handleChange.bind( this );
    this.state = {
      password: "",
      username: ""
    };

    // -- Demo material, don't actually do this in a production site
    this.security = new security();
    this.verySecureDatabase = {
      username: [ "cc0b8a95a883bc0b5f64a536de42349e0ce0673918a3c446255ddc8354887140" ],
      password: [ "cf1f7d3aa1a6b12eac16235179466c6fe394deec00fefcc2bba75cb329ad97f8" ],
      salt: [ "AbCbT123" ]
    };

  }

  handleChange( e ) {
    switch( e.target.name ) {
      case "password":
        this.setState({ password: e.target.value });
        break;
      case "username":
        this.setState({ username: e.target.value });
        break;
      default:
        console.error( `no such target ${e.target.name}` );
    }
  }

  // -- This would trigger a Redux 'Action' in the real world
  handleLogin = () => {
    const username = this.state.username;
    const password = this.state.password;

    console.group( "handleLogin" );
    console.debug( "Login Action:", username, password );

    let result = {
      type: null,
      auth: false
    };

    const digestUN = this.security.generateDigest( username, null, null );
    const validUser = this.verySecureDatabase.username.find( elem => elem === digestUN );
    if( !!validUser ) {
      const user = this.verySecureDatabase.username.indexOf( digestUN );
      const passwordDB = this.verySecureDatabase.password[ user ];
      const saltDB = this.verySecureDatabase.salt[ user ];
      result.auth = this.security.compareDigest( passwordDB, password, saltDB );
    }
    else {
      console.info( "No valid user:", digestUN );
    }

    result.type = "login";

    console.debug( "Result:", result );
    console.groupEnd( "handleLogin" );

    this.props.setAuth( result.auth );

  }

  render() {
    return(
      <div className = "Flex-container">
        <h3>Login system demo:</h3>
        <span>
          Note: all credentials are for demonstration purposes only, they are not linked to real accounts.
        </span>
        <p>
          This demo uses salts, peppers, and hashes to store the credentials.
          When clicking <i>Login</i> notice how it's not instant.
          Behind the scenes, the password's hash digest is being recomputed with different pepper values which are not saved.
          Open your browser's debugging tools with <code>f12</code> to see exactly what is happening.
          In a production website, these logs would not be available to the users.
        </p>
        <div>
          <input
            name = "username"
            rows = "1"
            type = "text"
            placeholder = "username: connor"
            onChange = { this.handleChange }
          />
          &nbsp;
          <input
            name = "password"
            rows = "1"
            type = "password"
            placeholder = "password: password"
            onChange = { this.handleChange }
          />
        </div>
        <div>
          <button
            id = "clickable"
            onClick = { () => this.handleLogin() }
          >
            Login
          </button>
        </div>
        <p 
          id = "clickable"
          onClick = { () => this.props.changePage( "/demos/protected" ) } 
        >
          Click here to be directed towards the protected page. It should kick you out.
        </p>
      </div>
    );
  }
}

export default LoginPage;
