import { Route, Switch, Redirect, BrowserRouter } from "react-router-dom";
import React from "react";
import { createHashHistory } from "history";
import Home from "./Home.js";
import Portfolio from "./Portfolio.js";
import Blog from "./Blog.js";
import Demos from "./Demos/Demos.js";
import LoginPage from "./Demos/authSystem/Login.js";
import ProtectedPage from "./Demos/authSystem/Protected.js";
import Ribbon from "./Ribbon.js";
import YYYYYY from "./Demos/games/YYYYYY.js";

const Router = () => {

  let history = createHashHistory({
    forceRefresh: true,
  });

  let authState = false;
  try {
    authState = !!history.location.state.auth;
  } catch( e ) {
    console.info( "no auth" );
  }

  const setAuth = ( auth ) => {
    if( auth === true ) {
      authState = auth;
      history.push( "/demos/protected", { auth: auth } );
    }
    else {
      authState = false;
      history.push( "/demos/login", { auth: false } );
    }
  }

  const changePage = ( to ) => {

    console.debug( `navigating to: ${to}` );

    switch( to ) {
      case "home":
      case "/":
        history.push( "/" );
        break;
      case "/portfolio":
        history.push( "/portfolio" );
        break;
      case "/blog":
        history.push( "/blog" );
        break;
      case "/demos":
        history.push( "/demos" );
        break;
      case "/demos/YYYYYY":
        history.push( "/demos/YYYYYY" );
        break;
      case "/demos/login":
        history.push( "/demos/login" );
        break;
      case "/demos/protected":
        history.push( "/demos/protected" );
        break;
      default:
        console.error( `Nice dude, ${to} doesn't exist` );
    }
  }

  return(
    <BrowserRouter basename = { "#" } >
      <Ribbon changePage = { ( to ) => changePage( to ) } />
      <Switch>
        <Route exact path = "/">
          <Home changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = "/portfolio">
          <Portfolio changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = "/blog">
          <Blog changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = "/demos">
          <Demos changePage = { ( to ) => changePage( to ) } />
        </Route>
        <Route exact path = "/demos/YYYYYY">
          <YYYYYY/>
        </Route>
        <Route exact path = "/demos/login">
          <LoginPage
            setAuth = { ( auth ) => setAuth( auth ) }
            changePage = { ( to ) => changePage( to ) }
          /> 
        </Route>
        <Route exact path = "/demos/protected"
          render = { ( props ) =>
            authState ? ( 
              <ProtectedPage
                setAuth = { ( auth ) => setAuth( auth ) }
                changePage = { ( to ) => changePage( to ) }
              /> 
            )
            : (
              <Redirect
                to = {{
                  pathname: "/demos/login",
                  state: {
                    from: props.location
                  }
                }}
              />
            )
          }
        />
        <Route path = "*">
          <h3>404 BruH This page don't exist</h3>
          <button onClick = { () => changePage( "home" ) } >Eject</button>
        </Route>
      </Switch>
    </BrowserRouter>
  );

};

export default Router;
